import React, { Component, useState } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../../Botones/BackB';
import './EjemplosRV.css';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function ExamplesRV() {

        const opts = {
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 0
            },
        };

        const [state, toggle] = useState(true)
        const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoEjemplos2">
                <Container fluid className="ContaiEjemplosRV">
                    <Row className="RowTitEjemplosRV">
                    <Typical
                            steps={['Ejemplos de aplicación de la RV', 500]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowEjemplosRV">
                        <Col className="ColEjemploRV" xl={4}>
                            <div>
                                <h2>Google Expeditions</h2>
                                <p>Es una <strong>aplicación educativa</strong> que hace uso de más de 900 expediciones en RV. Mediante videos en 360°, permite a los estudiantes
                                <strong> explorar</strong> el mundo, a la vez que les presenta información sobre <strong>historia, ciencia, artes, etc</strong>, a lo largo del
                                 recorrido.</p>
                                <YouTube className="VideoEjemploRV" videoId="LKyHg-31jHM" opts={opts}/>
                            </div>
                        </Col>
                        <Col className="ColEjemploRV" xl={4}>
                            <div>
                                <h2>El Gran Reto</h2>
                                <p>Es una actividad educativa en RV donde los participantes pueden <strong>experimentar</strong> 10 entornos diferentes y <strong>resolver</strong> los 
                                retos que se les van presentando durante el recorrido, a través de contenidos divertidos y <strong>gamificados</strong> que refuerzan sus conocimientos.</p>
                                <YouTube className="VideoEjemploRV" videoId="VqpwWPFKniU" opts={opts}/>
                            </div>                        
                        </Col>
                        <Col className="ColEjemploRV" xl={4}>
                            <div>
                                <h2>Mondly VR</h2>
                                <p>Es una experiencia para aprender <strong>idiomas</strong>, donde se tienen <strong> conversaciones</strong> reales en situaciones específicas. Los usuarios
                                    pueden elegir entre 30 <strong>idiomas</strong> diferentes, y los escenarios en los que desean practicarlo.</p>
                                <YouTube className="VideoEjemploRV" videoId="oZy_UhP9S_A" opts={opts}/>
                            </div>                        
                        </Col>
                    </Row>                                        
                </Container>
                <div className="BtnBack" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Continuo" text="Regresar"></Rboton>
                    </animated.div>
                </div>
            </div>
        );
    }

export default ExamplesRV;