import React, { useState } from 'react';
import './RAGeo.css';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../../Botones/BackB';
import WallaMe from '../../Images/Aplicaciones/WallaMe.svg';
import Wiki from '../../Images/Aplicaciones/Wiki.svg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function RAGeo()  {
    

        const opts = {
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 1
            },
        };

        const [state, toggle] = useState(true)
        const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoMarker">
                <div id="Concepto">
                    <Container fluid className="ContaiMarker">
                        <Row className="TitRAMarker">
                            <Typical
                                steps={['Realidad Aumentada por geolocalización', 300]}
                                loop={Infinity}
                                wrapper="h1"
                            />                            
                        </Row>
                        <Row className="RowInfoMarker">
                            <Col lg={6} sm={12}>
                                <p>Este tipo de RA se basa en el uso de información obtenida por el <strong>GPS</strong> y el <strong>internet</strong> para crear experiencias
                                únicas para cada usuario, ya que depende del lugar donde se encuentre.</p>
                            </Col>
                            <Col lg={6} sm={12}>
                                <YouTube className="VideoMarker" videoId="3PUg-BqSzuw" opts={opts}/>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="Aplicaciones">
                    <Container fluid className="ContaiRAAppsGeo">
                        <Row className="RowTitRAApps">
                            <Typical
                                steps={['Aplicaciones de RA por geolocalización', 500]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowApp">
                            <Col className="ColInfoRAApp" xl={7}>
                                <h2>WallaMe</h2>
                                <p>Es una aplicacción que permite <strong>crear</strong> y <strong>descubrir</strong> mensajes que han sido colocados en un punto específico en el <strong>mapa</strong>. Cuenta con plantillas que pueden ser usadas por ejemplo en yincanas. O 
                                en un espacio en el que la idea sea fomentar la exporación.</p>
                                <a className="aLink" href="http://walla.me/" target="_blank"><span>Visitar</span></a>
                            </Col>
                            <Col id="Imagen1O" className="ColImgRAApp" xl={5}>
                                <img src={WallaMe}></img>
                            </Col>
                        </Row>                        
                        <Row className="RowApp">
                            <Col id="InfoO" className="ColInfoRAApp2" xl={7}>
                                <h2>Wikitude</h2>
                                <p>Fue la <strong>primera aplicación</strong> pública que hizo uso del GPS y brújula de los dispósitivos para el despliegue de experiencias de Realidad Aumentada.
                                    Parte de la información que despliega es gracias a su vinculación con wikipedia, tripadvisor y redes sociales.</p>
                                    <a className="aLink" href="https://www.wikitude.com/" target="_blank"><span>Visitar</span></a>
                            </Col>
                            <Col id="Imagen2O" className="ColImgRAApp2" xl={5}>
                                <img src={Wiki}></img>
                            </Col>                                                        
                        </Row>
                    </Container>
                </div>
{/* 
                <div id="MarcadoresRA">
                    <Container fluid className="ContaiConsejos">
                        <Row className="RowTitRAApps">
                            <Typical
                                steps={['Cómo elegir o crear un marcador', 500]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoConsejos">
                            <Col className="ColImgMarker" xl={6}>
                                <img src={ej1}></img>
                            </Col>
                            <Col className="ColInfoConsejos" xl={6}>
                                <p>Las tecnologías emergentes, nostra feugiat varius in tempor magnis laoreet, torquent habitasse cras nullam praesent duis. Risus dapibus pharetra 
                                    duis posuere dui neque elementum, nostra feugiat varius in tempor magnis laoreet.</p>                                    
                                <a className="aLink" href='http://www.aumentaty.com/community/es/markers/' target="_blank"><span>Consejos</span></a>
                            </Col>
                        </Row>
                    </Container>
                </div> */}


                <div className="BotVolType" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Types" text="Regresar"></Rboton>
                    </animated.div>
                </div>


                <Nav className="Bottombar">                                  
                    <div className="MenuBo">          
                        <Nav.Link href="#Concepto"><div className="Campo">Concepto</div></Nav.Link>
                        <Nav.Link href="#Aplicaciones"><div className="Campo">Aplicaciones</div></Nav.Link>
                    </div>
                </Nav>
                
            </div>
        );
    }

export default RAGeo;