import React, { useState } from 'react';
import './RAEspacial.css';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../../Botones/BackB';
import Hope from '../../Images/Aplicaciones/Hope.svg';
import arloopa from '../../Images/Aplicaciones/Arloopa.svg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';



function RAEspacial() {
    const opts = {
        playerVars: {
            autoplay: 1
        },
    };       

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

    return(            
        <div className="TodoEspacial">
            <div id="ConceptoEs">
                <Container fluid className="ContaiEspacial">
                    <Row className="TitRAEspacial">
                        <Typical
                            steps={['Realidad Aumentada espacial - Smart Terrain', 300]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowInfoEspacial">
                        <Col lg={6} sm={12}>
                            <p>Smart Terrain es el <strong>reconocimiento</strong> del <strong>entorno físico</strong>. Este tipo de RA es capaz de <strong>detectar</strong> las
                                superficies, <strong>recreando</strong> lo real dentro de la aplicación, lo que permite posicionar objetos virtuales que <strong>interactúen </strong>
                                con nuestro entorno de manera mucho más real.</p>
                        </Col>
                        <Col lg={6} sm={12}>
                            <YouTube className="VideoEspacial" videoId="MGJ-FA5IIj0" opts={opts}/>
                        </Col>
                    </Row>
                </Container>
            </div>

            <div id="AplicacionesEs">
                <Container fluid className="ContaiRAEspa">
                    <Row className="RowTitRAAppsEs">
                        <Typical
                            steps={['Aplicaciones de RA espacial', 300]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowAppEs">
                        <Col className="ColInfoRAApp" xl={7}>
                            <h2>Hope - Realiad Aumentada</h2>
                            <p>Es una aplicación de RA para aprender y despertar la curiosidad por las <strong>ciencias</strong> de forma <strong>divertida</strong> e 
                            <strong> innovadora</strong>. Esta aplicación cuenta con temas de astronomía, animales, cuerpo humano y dinosaurios que se pueden desplegar
                            sobre la <strong>superficie</strong> que elijas.</p>
                            <a className="aLink2" href="https://www.hope.com.pe/" target="_blank"><span>Visitar</span></a>
                        </Col>
                        <Col id="Imagen1" className="ColImgRAApp" xl={5}>
                            <img src={Hope}></img>
                        </Col>
                    </Row>
                    <Row className="RowAppEs">
                        <Col id="Imagen2A" className="ColImgRAApp3" xl={5}>
                            <img src={arloopa}></img>
                        </Col>
                        <Col id="App2" className="ColInfoRAApp2" xl={7}>
                            <h2>ARLOOPA</h2>
                            <p>Es una <strong>plataforma</strong> que contiene diferentes aplicaciones de RA que se pueden desplegar de 3 formas: basadas en <strong>marcadores</strong> ,
                            posicionamiento <strong>espacial</strong> y basadas en <strong>geolocaclización.</strong></p>
                                <a className="aLink2" href="https://app.arloopa.com/" target="_blank"><span>Visitar</span></a>
                        </Col>                            
                    </Row>
                </Container>
            </div>

            <div className="BotVolType" onClick={() => toggle(!state)}>
                <animated.div
                    style={{
                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                    transform: x
                        .interpolate({
                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                        })
                        .interpolate(x => `scale(${x})`)
                    }}>
                    <Rboton link="Types" text="Regresar"></Rboton>
                </animated.div>
            </div>

            <Nav className="BottombarE">                                  
                <div className="MenuBo">          
                    <Nav.Link href="#ConceptoEs"><div className="Campo">Concepto</div></Nav.Link>
                    <Nav.Link href="#AplicacionesEs"><div className="Campo">Aplicaciones</div></Nav.Link>
                </div>
            </Nav>                
        </div>
    );
}

export default RAEspacial;