import React, { Component, useState } from 'react';
import { Container, Col, Row } from 'react-bootstrap';
import RAClases from '../../Images/Ra_Import.svg';
import './RAImportan.css';
import Rboton from '../../Botones/BackB';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function RAImportan() {
    

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoImport">
                <Container fluid className="ContaiImportan">
                    <Col className="ColInfoImport" xl={7}>
                        <Row className="RowTitImportan">
                            <Typical
                                steps={['Importancia de la RA en las clases', 500]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowTextImport">
                            <p>La Realidad Aumetada está revolucionando los procesos educativos, pues combina imágenes del mundo real con elementos digitales para crear nuevos entornos 
                                con información complementaria. Entornos que por sus contenidos interactivos fomenta en los estudiantes la capacidad de investigación, haciendo que el aprendizaje sea mucho más rápido y efectivo.</p>
                        </Row>
                        <Row className="RowEnlaces">
                            <h2>Enlaces a artículos</h2>
                            <a href="https://drive.google.com/file/d/1htwRyX9DjV7lh-vS1TeuAUIG4Nt6Zzuy/view?usp=sharing" target="_blank">¿Mejora la Realidad Aumentada el aprendizaje de los alumnos? - Museo aumentado</a>
                            <a href="https://drive.google.com/file/d/1DxM15zRyq1FzgyBf8bdpPXEKrhD7AheE/view?usp=sharing" target="_blank">EduTrends - Realidad Aumentada y Virtual</a>
                            <a href="https://drive.google.com/file/d/10EvRyzFU-w-Mi7Vcm-JrFFzn4KfxKOtn/view?usp=sharing" target="_blank">Realidad Aumentada en Educación</a>
                        </Row>
                    </Col>
                    <Col className="ColImgImporta" xl={5}>
                        <img src={RAClases}></img>                    
                    </Col>
                </Container>
                <div className="BotnBac" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Continuo" text="Regresar"></Rboton>
                    </animated.div>
                </div>
            </div>
        );
    }

export default RAImportan;