import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import tech from '../../Images/RV/RV_Tech.svg';
import './Technology.css';
import Side2 from '../RV/Sidebar2';
import Typical from 'react-typical';

class Technology extends Component {
    render() {
        return(
            <div className="TodoTech">
                <Side2></Side2>
                <Container fluid className="ContaiTech">
                    <Row className="RowTitTech">
                        <Typical
                            steps={['Tecnologías de la Realidad Virtual', 700]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowTextTech">
                        <Col className="ColTextTech2">
                            <p>En esta sección encontrarás algunas de las tecnologías con las que se pueden desplegar experiencias de Realidad Virtual.</p>
                            <a className="aLink" href="HMD"><span>Saber más</span></a>
                        </Col>
                        <Col className="ColImgTech">
                            <img className="imgTechn"src={tech}></img>
                        </Col>                        
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Technology;