import React, { useState } from 'react';
import Rboton from '../Botones/OmitirB';
import '../Intro/Intro.css';
import { Container, Row, Col } from 'react-bootstrap';
import YouTube from 'react-youtube';
import { useSpring, animated } from 'react-spring';


function Intro() {   

  const [state, toggle] = useState(true)
  const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

  const opts = {
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 1
    }
  }

  return (
    <div className="Alles">           
      <Container className="Contenedor">
        <Row>                    
          <Col sm={12} lg={12} className="Video">
            <div className="cont">
              <h1 className="TitleTec">Tecnologías emergentes</h1>
              <p>La <strong>Realidad Aumentada</strong> y la <strong>Realidad Virtual</strong> son tecnologías emergentes en la educación, donde gobiernos, empresas y expertos 
              en educación están buscando la forma de integrarlas con el enfoque <strong>STEAM</strong>.  
              </p>
              <div className="content">
                <div className="Boton" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                      <Rboton link="Navigation" text="Omitir"></Rboton>
                    </animated.div>
                </div>
                <YouTube className="nada" videoId="ONeSKIMSNjE" opts={opts}/>                 
              </div>                                                    
            </div>
          </Col>
        </Row>                            
      </Container>                       
    </div>
  );       
}   

export default Intro;