import React, {useState} from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import './HerramientasRA.css';
import { useSpring, animated } from 'react-spring';
import Rboton from '../../Botones/BackB'
import YouTube from 'react-youtube';
import Typical from 'react-typical';

function HerramientasRA() {

    const opts = {
        playerVars: {
          autoplay: 0
        },
    };

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

    return(
        <div className="TodoHerraRA">
            <div id="Assemblr">
                <Container fluid className="ContaiAssStudio">
                    <Row className="RowTitHerraRA">
                        <Typical
                            steps={['Herramientas para la creación de RA', 800]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowAssStudio">
                        <Col>
                            <YouTube className="VideoAssemblrStudio" videoId="yl3J5sxm5aY" opts={opts}/>
                        </Col>
                        <Col className="ColInfoAssSt">
                            <h2>ASSEMBLR STUDIO</h2>
                            <p>Es la versión de escritorio de la plataforma Assemblr, con la cual podemos crear <strong>experiencias</strong> de Realidad Aumentada de forma
                            más cómoda. En el tutorial verás cómo se <strong>crea un proyecto</strong>, aprenderás a <strong>diseñar contenido</strong> con las herramientas que
                             nos brinda Assemblr y conocerás cómo <strong>publicar tu proyecto</strong> para poder visualizarlo desde un dispositivo móvil.</p>
                            <a href="https://assemblrworld.com/studio" target="_balnk">Descarga el programa aquí</a>
                        </Col>
                    </Row>
                </Container>
                <Container fluid className="ContaiAssStudio">                    
                    <Row className="RowAssStudio">
                        <Col className="ColInfoAssApp">
                            <h2 id="Titulo2">ASSEMBLR APP</h2>
                            <p>Es la aplicación que nos permite <strong>visualizar</strong> proyectos de Realidad Aumentada. Además, cuenta con la opción de crear un proyecto, por 
                            lo que podremos crear nuestras experiencias en <strong>cualquier lugar</strong> al que vayamos. En el tutorial conocerás las diferentes 
                            <strong> opciones</strong> con las que cuenta la App para la <strong>visualización</strong> de proyectos.</p>
                            <a href="https://assemblrworld.com/mobile-app" target="_balnk">Descarga la aplicación aquí</a>
                        </Col>
                        <Col>
                            <YouTube id="video2" className="VideoAssemblrStudio" videoId="sRhgETpuKAc" opts={opts}/>
                        </Col>                        
                    </Row>
                </Container>                
            </div>          
            <div className="BotVolExp" onClick={() => toggle(!state)}>
                <animated.div
                    style={{
                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                    transform: x
                        .interpolate({
                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                        })
                        .interpolate(x => `scale(${x})`)
                    }}>
                    <Rboton link="ARTools" text="Regresar"></Rboton>
                </animated.div>
            </div>  
        </div>
    );
}

export default HerramientasRA;