import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import './Experiencias.css';
import CreativeImg from '../Images/CreaExpe.svg';
import Rboton from '../Botones/OmitirB';
import Fase111 from '../Images/Fases/Fase1.svg'
import Fase2 from '../Images/Fases/Fase2.svg'
import Fase3 from '../Images/Fases/Fase3.svg'
import Acercamiento from '../Images/Experiencias/Acercamiento.png';
import Descubriendo from '../Images/Experiencias/Descubriendo.png';
import Prototipado from '../Images/Experiencias/Prototipado.png';
import im from '../Images/QuiverVision.svg';
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import Audio1 from '../Audios/Diseño experiencias.mp3';
import Audio2 from '../Audios/Pruba diagnostica1.mp3';
import Audio3 from '../Audios/Experiencias diseñadas.mp3';
import Audio4 from '../Audios/Despliegue experiencia.mp3';
import EstrucImg from '../Images/Fases/Estrucuta2.svg';
import { Link } from 'react-router-dom';
import Typical from 'react-typical';
import Carousel from 'react-bootstrap/Carousel'
import First from '../Images/Despliegue/1.jpg';
import Second from '../Images/Despliegue/2.JPG';
import Thirth from '../Images/Despliegue/3.JPG';

class Exp extends Component {
    render() {
        return(
            <div className="TodoExp">
                <div id="DisEx">
                    <Container fluid className="ContainExper">
                        <Row className="RowExpe">
                            <Col className="ConteniExp" lg={6}>
                                <div className="TextEx">
                                    <Typical
                                        steps={['Diseño de experiencias de RA y RV bajo enfoque STEAM', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />
                                    <p>Descubre cómo se diseñan y desarrollan experiencias STEAM de Realidad Aumentada y Realidad Virtual al estilo CONECTA-R con STEAM.</p>
                                    <div className="AudioInExperGene">
                                        <AudioPlayer 
                                            autoPlay
                                            src={Audio1}                                    
                                            showJumpControls ={false}                                            
                                        />
                                    </div>
                                    
                                </div>                                                      
                                
                            </Col>
                            <Col className="ColImgIntro" lg={6}>
                                <img src={CreativeImg}></img>
                            </Col>
                            <div className="IndInici"><a href="#Estructura"><h2>Iniciar </h2></a></div>
                        </Row>
                    </Container>
                </div>

                <div id="Estructura">
                    <Container fluid className="ContainEstruc1">
                        <Row className="RowEstruc1">
                            <Col className="ColTextInfoExper" xl={6}>
                                <Typical
                                    steps={['Astronomía a través de la RA', 1000]}
                                    loop={Infinity}
                                    wrapper="h1"
                                />
                                <p>Es una experiencia que busca que los participantes identifiquen el uso de las tecnologías emergentes (Realidad Aumentada) como herramienta 
                                    de apoyo para el proceso de aprendizaje, especialmente en áreas como la Astronomía, donde la idea es despertar el interés por la ciencia, 
                                    mostrando los avances tecnológicos en el campo espacial, su uso cotidiano, los grandes descubrimientos y el instrumental que los ha hecho 
                                    posibles.<br></br><br></br>Para su desarrollo, se hizo uso de la metodología descrita en la imagen de la derecha, en donde también podemos
                                    observar que está compuesta de 3 micro experiencias.</p>
                                <div className="Fas1"><a href="#Fase1"><h2>Etapa 1 </h2></a></div>
                            </Col>                            
                            <Col xl={6} className="ImgEs">
                                <img src={EstrucImg}></img>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="Fase1">
                    <Container fluid className="ContainFase1">
                        <Row className="RowConteniFase1">
                            <Col className="ColInfoFase1" xl={7}>
                                <Row className="RowTitFase1">
                                    <Typical
                                        steps={['Prueba diagnóstica', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />                                  
                                </Row>
                                <Row className="RowInfoFase1">
                                    <Col xl={5}>
                                        <img src={Fase111}></img>
                                    </Col>
                                    <Col className="PaddingInfoFas1" xl={7}>
                                        <p>Esta primera etapa se encarga de <strong>identificar</strong> el contexto de la institución donde se va a desplegar la experiencia.</p>     
                                        <div className="AudioFase1">
                                            <AudioPlayer 
                                                autoPlay={0}
                                                src={Audio2}                                    
                                                showJumpControls ={false}                                            
                                            />  
                                        </div>                                                                     
                                    </Col>
                                </Row>
                            </Col>
                            <Col className="ColEntregablesFase1" xl={5}>
                                <Row className="RowInfoEntreFase1">
                                    <div className="TextEntreFase1">
                                        <h2>Entregables</h2>
                                        <p>Una ficha que recopila y detalla toda la información sobre la población, tecnologías y espacios con los que cuenta la institución.</p>
                                    </div>                                    
                                    <img src={im}></img>
                                </Row>
                                <Row>
                                    <Rboton text="Ver"></Rboton>
                                </Row>
                            </Col>
                            <div className="Fas2"><a href="#Fase2"><h2>Etapa 2 </h2></a></div>
                        </Row>
                    </Container>
                </div>

                <div id="Fase2">
                    <Container fluid className="ContainFase2">
                        <Row className="RowConteniFase2">
                            <Col className="ColInfoFase2" xl={7}>
                                <Row className="RowTitFase2">
                                    <Typical
                                        steps={['Diseño de experiencias', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />
                                </Row>                                
                                <Row className="RowInfoFase2">
                                    <Col xl={5}>
                                        <img src={Fase2}></img>
                                    </Col>
                                    <Col className="PaddingInfoFas1" xl={7}>
                                        <p>Momento de pensar en las <strong>actividades STEAM</strong> junto con los <strong>recursos de apoyo</strong> que harán parte de la experiencia.</p>  
                                        <div className="AudioFase1">
                                            <AudioPlayer 
                                                autoPlay={0}
                                                src={Audio3}                                    
                                                showJumpControls ={false}                                            
                                            />  
                                        </div>                                  
                                    </Col>
                                </Row>
                            </Col>                            
                            <Col className="ColEstructExperi" xl={5}>
                                <Row className="RowInfoEstruExp">
                                    <h2>Micro Experiencias</h2>
                                    <p>Haz clic para conocer cada una de ellas.</p>
                                </Row>
                                <Row className="RowdEstrucEsper">
                                    <Col className="ColEstruE">
                                        <Row className="RowImgs">
                                            <Col className="ColImgs">
                                                <Link to="AstronomiaRA"><div className="hola"><span className="tooltiptext">Acercamiento Astronomía <br></br>con RA</span><img className="imgzoE" src={Acercamiento}></img></div></Link>
                                            </Col>
                                            <Col>

                                            </Col>                                   
                                        </Row>
                                        <Row className="RowImgs">
                                            <Col>
                                                
                                            </Col>
                                            <Col className="ColImgs">
                                                <Link to="DescubreRA"><div className="hola"><span className="tooltiptext">Descubriendo la Realidad Aumentada</span><img className="imgzoE" src={Descubriendo}></img></div></Link>
                                            </Col>                                   
                                        </Row>
                                        <Row className="RowImgs">
                                            <Col className="ColImgs">
                                                <Link to="Prototipado"><div className="hola"><span className="tooltiptext">Prototipando mi primer experiencia</span><img className="imgzoE" src={Prototipado}></img></div></Link>
                                            </Col>
                                            <Col>
                                                
                                            </Col>                                   
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                            <div className="Fas3"><a href="#Fase3"><h2>Etapa 3 </h2></a></div>
                        </Row>
                    </Container>
                </div>

                <div id="Fase3">
                    <Container fluid className="ContainFase3">
                        <Row className="RowTitFase3">
                            <Typical
                                steps={['Despliegue de las experiencias', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowContentFase3">
                            <Col className="ColInfoFase3">
                                <div className="divFase3">
                                    <p>La tercera y ultima etapa es la liberación de la experiencia, mediante el despliegue de las actividades en la institución educativa.</p>
                                    <div className="AudioFase1">
                                        <AudioPlayer 
                                            autoPlay={0}
                                            src={Audio4}                                    
                                            showJumpControls ={false}                                            
                                        />  
                                    </div>
                                </div>  
                                <div className="ImgFase3">
                                    <img src={Fase3}></img>
                                </div>                          
                            </Col>
                            <Col>

                                <Carousel>
                                    <Carousel.Item className="CImg">
                                        <img
                                        className="d-block w-100"
                                        src={First}
                                        alt="First slide"
                                        />
                                        <Carousel.Caption>
                                            <h3>Semana STEAM</h3>
                                            <p>Grupo de estudiantes de grado tercero del colegio La Fontaine.</p>
                                        </Carousel.Caption>
                                    </Carousel.Item>
                                    <Carousel.Item className="CImg">
                                        <img
                                        className="d-block w-100"
                                        src={Second}
                                        alt="Third slide"
                                        />
                                        <Carousel.Caption>
                                            <h3>Construcción</h3>
                                            <p>Proceso de creación.</p>
                                        </Carousel.Caption>
                                    </Carousel.Item>
                                    <Carousel.Item className="CImg">
                                        <img
                                        className="d-block w-100"
                                        src={Thirth}
                                        alt="Third slide"
                                        />
                                        <Carousel.Caption>
                                            <h3>Merge Cube</h3>
                                            <p>Construido por uno de los estudiantes.</p>
                                        </Carousel.Caption>
                                    </Carousel.Item>
                                </Carousel>
















                                <img></img>                            
                            </Col>
                        </Row>
                    </Container>
                </div>
            </div>
        );
    }
}

export default Exp;