import React from 'react';
import {Nav} from 'react-bootstrap';
import Rboton from '../../Botones/BackB';
import './Sidebar2.css';
import Flecha from '../../Images/Flecha.svg';

const Side = () => (
    <div className="contentSidebar">
        <Nav className="Sidebar">
            <Nav.Link href="/HistoryVR"><div className="Celda"><img src={Flecha}></img>Historia</div></Nav.Link>
            <Nav.Link href="/Perifericos"><div className="Celda"><img src={Flecha}></img>Periféricos</div></Nav.Link>
            <Nav.Link href="VRTypes"><div className="Celda"><img src={Flecha}></img>Tipos</div></Nav.Link>
            <Nav.Link href="Technology"><div className="Celda"><img src={Flecha}></img>Tecnologías</div></Nav.Link>
            <div className="bot">
                <Rboton link="Continuo" text="Regresar"></Rboton>
            </div>            
        </Nav>
    </div>
)

export default Side;