import React, {useState}  from 'react';
import { Container, Row } from 'react-bootstrap';
import imgRv from '../../Images/RV/RVInmersive.svg';
import Audios from '../../Audios/RV inmersiva.mp3';
import AudioPlayer from 'react-h5-audio-player';
import Rboton from '../../Botones/BackB';
import './RVInmersiva.css';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

const RVInmersiva = () => {
    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})
    return(
        <div className="TodoInmer">
            <Container className="ContainRV">
                <Row className="RowtitRV">
                    <Typical
                        steps={['Realidad Virtual inmersiva', 1000]}
                        loop={Infinity}
                        wrapper="h1"
                    />
                </Row>
                <Row className="RowInfoRV">
                    <img id="ImIn" src={imgRv}></img>
                    <AudioPlayer className="AudioRV"
                        autoPlay
                        src={Audios}                                    
                        showJumpControls ={false}                                            
                    />
                </Row>
            </Container>
            <div className="btnB" onClick={() => toggle(!state)}>
                <animated.div
                    style={{
                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                    transform: x
                        .interpolate({
                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                        })
                        .interpolate(x => `scale(${x})`)
                    }}>
                    <Rboton link="VRTypes" text="Regresar"></Rboton>
                </animated.div>
            </div>            
        </div>
    );
}

export default RVInmersiva;