import React, { Component } from 'react';
import './History.css';
import { Container, Row, Col } from 'react-bootstrap';
import Side from './Sidebar';
import Typical from 'react-typical';

import TimeLine from './Timeline/Timeline';

class History extends Component {    
    render() {        
        return(          
            <div className="ContHist">
                <Side></Side>
                <Container fluid className="contaiHistor">
                    <Row className="titlHis">
                        <Typical
                            steps={['Historia de la Realidad Aumentada', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="InfoHisto">
                        <Col className="col2" xl={{ span: 9, offset: 2 }}>   
                            <TimeLine></TimeLine>
                        </Col>
                    </Row>
                </Container>        
            </div>
        );
    }
}

export default History;