import React from 'react';
import {Nav} from 'react-bootstrap';
import Rboton from '../Botones/BackB';
import '../ConceptosRAyRV/Sidebar.css';
import Flecha from '../Images/Flecha.svg';

const Side = () => (
    <div className="contentSidebar">
        <Nav className="Sidebar">
            <Nav.Link href="/History"><div className="Celda"><img src={Flecha}></img>Historia</div></Nav.Link>
            <Nav.Link href="/Funtion"><div className="Celda"><img src={Flecha}></img>Función</div></Nav.Link>
            <Nav.Link href="/Types"><div className="Celda"><img src={Flecha}></img>Tipos</div></Nav.Link>
            <Nav.Link href="/ARTools"><div className="Celda"><img src={Flecha}></img>Herramientas</div></Nav.Link>            
            <div className="bot">
                <Rboton link="Continuo" text="Regresar"></Rboton>    
            </div>            
        </Nav>
    </div>
)

export default Side;