import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import './STEAM.css';
import steampic from '../../Images/STEAM.png';
import ConeSte from '../../Images/Exagono2.png';
import EjesConectar from '../../Images/Ejes.png'
import Steamtriple from '../../Images/InfoSTEAM.svg';
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import Audio1 from '../../Audios/STEAM.mp3';
import FotoConectar from '../../Images/ConectarPic.png';
import CardDise from '../../Images/Fases/DiseñoExper.svg'
import Typical from 'react-typical';
import Card_Ejes from '../../Images/Fases/Card_ejes.svg';
import Estru from '../../Images/Fases/Estructura1.svg';

class Steam extends Component{
    render(){
        return(
            <div className="TodoSteamSection">
                <div id="IntroSectionSteam">
                    <Container fluid className="ContaiIntroSteam">
                        <Row className="RowTitIntroSteam">
                            <Typical
                                steps={['Generalidades STEAM', 500, 'Generalidades CONECTA-R con STEAM', 300]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowTextIntroSteam">
                            <p>En esta sección darás un recorrido por dos temas. El primero se trata de una breve <strong>descripción</strong> del enfoque <strong>STEAM</strong> "y sus
                             conceptos pedagógicos". El segundo, es una revisión del Programa <strong>CONECTA-R con STEAM</strong> de la <strong>Universidad Autónoma de Occidente</strong> (UAO),
                              y la forma en la que ha adaptado el enfoque STEAM para el <strong>diseño de Experiencias</strong>.</p>
                             <div className="InstrucciónInicio">
                                <p>Haz clic en uno de los hexagonos o scroll para comenzar</p>                                
                             </div>                            
                        </Row>
                        <Row className="RowImgDosPartes">
                            <Col>
                                <a href="#AboutSTEAM"><img className="imgST" src={steampic}></img></a>
                            </Col>
                            <Col>
                                <a href="#Conectar"><img className="imgST" src={ConeSte}></img></a>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="AboutSTEAM">
                    <Container fluid className="ContaiAboSteam">
                        <Row className="DivInfoAS">
                            <Col className="ColInfoAbSteam" xl={6}>
                                <Row className="RowTitAbSteam">
                                    <Typical
                                        steps={['¿Qué es STEAM?', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />
                                </Row>
                                <Row className="RowInfoAbSteam">
                                    <p>STEAM es el acrónimo de los términos en inglés <strong>Science, Technology, Engineering, Art and Mathematics</strong>. Cinco disciplinas que son
                                    imprescindibles en la educación y han venido cogiendo fuerza en los últimos años, al trabajarse de manera <strong>transversal</strong> gracias al enfoque STEAM.</p>
                                    <AudioPlayer className="AudioSteam"
                                        autoPlay
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                    />
                                </Row>
                                <div className="F1"><h2></h2></div>
                            </Col>
                            <Col className="ColImgSteamEstr" xl={6}>
                                <img src={Steamtriple}></img>                        
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="Conectar">
                    <Container fluid className="ContaiConectar">
                        <Row className="RowTitConectar">
                            <Typical
                                steps={['CONECTA-R con STEAM', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowConectar">
                            <Col className="ColInfoConectar" xl={6}>
                                <img src={FotoConectar}></img>
                                <p>Es un programa que tiene como propósito contribuir a la <strong>apropiación fluida y responsable</strong> de la <strong>ciencia y la tecnología </strong> 
                                 en niños(as), docentes y padres de familia, mediante actividades orientadas por el enfoque <strong>STEAM</strong>.</p>
                                <p>Para mayor información, visita: <a href="https://conectar.uao.edu.co" target="_blank">conectar.edu.co</a></p>
                            </Col>
                            <Col className="ColImgConectarr" xl={6}>
                                <div className="Cardsejes">
                                    <a href="#EjesConectar"><img src={Card_Ejes}></img></a>
                                    <a href="#ExperienciasConectar"><img src={CardDise}></img></a>
                                </div>
                                <p>Haz clic en cada una para conocerlas</p>                                
                            </Col>
                            <div className="F2"><h2></h2></div>                            
                        </Row>
                    </Container>
                </div>

                <div id="EjesConectar">
                    <Container fluid className="ContaiEjes">
                        <Row className="RowTitEjes">
                            <Typical
                                steps={['Ejes del programa CONECTA-R', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowContenEjes">
                            <Col className="ColTextEjes" xl={5}>
                                <p>El programa CONECTA-R con STEAM ha decidido trabajar en <strong>seis líneas</strong> o ejes de acción en los cuales se alinean las experiencias.
                                 Estos ejes han sido pensados con el objetivo de <strong>fortalecer</strong> conocimientos, habilidades y aptitudes en STEAM.</p>     
                                <div className="F3"><h2></h2></div>                      
                            </Col>
                            <Col className="ColImgEjes" xl={7}>
                                <img src={EjesConectar}></img>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="ExperienciasConectar">
                    <Container fluid className="ContaiExperConec">
                        <Row className="RowTitExpConec">
                            <Typical
                                steps={['Experiencias del programa CONECTA-R', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowContenExpConec">                            
                            <Col className="ColImgExpCon" xl={6}>
                                <img src={Estru}></img>
                            </Col>
                            <Col className="ColTextExpCon" xl={6}>
                                <p>Para el diseño de experiencias orientadas bajo el enfoque STEAM, el Programa CONECTA-R ha definido una metodología que consta de 3 etapas
                                    .</p>     
                                <a className="aLink" href="Experiencias"><span>Saber más</span></a>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </div>
        );
    }
}

export default Steam;