import React, { Component, useState } from 'react';
import './RAMarker.css';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../Botones/BackB';
import Quiver from '../Images/QuiverVision.svg';
import Brain from '../Images/Aplicaciones/TheBrain.jpg';
import ej1 from '../Images/Ejemplo1.jpg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function RAMarker()  {

        const opts = {
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 1
            },
        };

        const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoMarker">
                <div id="Concepto">
                    <Container fluid className="ContaiMarker">
                        <Row className="TitRAMarker">
                            <Typical
                                steps={['Realidad Aumentada basada en marcadores', 300]}
                                loop={Infinity}
                                wrapper="h1"
                            /> 
                        </Row>
                        <Row className="RowInfoMarker">
                            <Col className="ColInfoMarker" lg={6} sm={12}>
                                <p>Este tipo de Realidad Aumentada hace uso de <strong>marcadores</strong> como activador para <strong>desplegar</strong> sobre ellos el contenido de RA 
                                cuando haya sido reconocido por la aplicación. Los marcadores pueden ser <strong>imágenes, logos, revistas</strong>, o incluso, <strong>códigos QR.</strong></p>
                            </Col>
                            <Col lg={6} sm={12}>
                                <YouTube className="VideoMarker" videoId="eDvq_yNQJqw" opts={opts}/>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="Aplicaciones">
                    <Container fluid className="ContaiRAApps">
                        <Row className="RowTitRAApps">
                            <Typical
                                steps={['Aplicaciones de RA de marcadores', 300]}
                                loop={Infinity}
                                wrapper="h1"
                            />                            
                        </Row>
                        <Row className="RowApp">
                            <Col className="ColInfoRAApp" xl={7}>
                                <h2>QuiverVision</h2>
                                <p>Es una aplicación que crea experiencias de <strong>coloreado inmersivas</strong> que estimulan la <strong>creativiad</strong> y la  
                                <strong> imaginación</strong> por medio de plantillas para colorear, con las que se puede observar la RA en entornos virtuales o minijuegos.</p>
                                <a className="aLink" href="https://quivervision.com/education-coloring-packs" target="_blank"><span>Visitar</span></a>
                            </Col>
                            <Col className="ColImgRAApp" xl={5}>
                                <img src={Quiver}></img>
                            </Col>
                        </Row>
                        <Row className="RowApp">
                            <Col id="App2" className="ColInfoRAApp2" xl={7}>
                                <h2>The Brain - AR Anatomy Series</h2>
                                <p>Aplicación que permite ver la <strong>anatomía</strong> del ser humano, desde la piel, hasta el hueso y el cerebro con todas sus partes. 
                                Todo al alcance de tu mano, gracias al marcador y a la aplicación que podemos descargar de forma gratuita.</p>
                                    <a className="aLink" href="https://www.harmony.co.uk/portfolio/the-brain-in-3d/" target="_blank"><span>Visitar</span></a>
                            </Col>
                            <Col id="Imagen2" className="ColImgRAApp2" xl={5}>
                                <img src={Brain}></img>
                            </Col>                                                        
                        </Row>
                    </Container>
                </div>

                <div id="MarcadoresRA">
                    <Container fluid className="ContaiConsejos">
                        <Row className="RowTitRAApps">
                            <Typical
                                steps={['Cómo elegir o crear un marcador de RA', 300]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoConsejos">
                            <Col className="ColImgMarker" xl={6} >
                                <img src={ej1}></img>
                            </Col>
                            <Col className="ColInfoConsejos" xl={6} >
                                <p>Tener un <strong>buen marcador</strong> de Realidad Aumentada nos <strong>permite</strong> que el contenido se <strong>despliegue</strong> de
                                 forma <strong>correcta</strong>, consiguiendo mejores resultados durante la experiencia.</p> 
                                <a className="aLink" href='http://www.aumentaty.com/community/es/markers/' target="_blank"><span>Ver consejos</span></a>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div className="BotVolType" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Types" text="Regresar"></Rboton>
                    </animated.div>
                </div>


                {/* <div className="BotVolType">
                    
                </div>                 */}
                <Nav className="Bottombar">                                  
                    <div className="MenuBo">          
                        <Nav.Link href="#Concepto"><div className="Campo">Concepto</div></Nav.Link>
                        <Nav.Link href="#Aplicaciones"><div className="Campo">Aplicaciones</div></Nav.Link>
                        <Nav.Link href="#MarcadoresRA"><div className="Campo">Cómo crear un marcador</div></Nav.Link>
                    </div>
                </Nav>
                
            </div>
        );
    }


export default RAMarker;