import React, { useState } from 'react';
import './RAObjects.css';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../../Botones/BackB';
import Quiver from '../../Images/QuiverVision.svg';
import Merge from '../../Images/Aplicaciones/Merge.svg';
import Warrior from '../../Images/Aplicaciones/Warrior.svg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function RAObjects() {   

        const opts = {
            playerVars: {
              autoplay: 1
            },
        };

        const [state, toggle] = useState(true)
        const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoObject">
                <div id="ConceptoOb">
                    <Container fluid className="ContaiObject">
                        <Row className="TitRAObject">
                            <Typical
                                steps={['Realidad Aumentada a través de objetos tangibles', 200]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoMarker">
                            <Col lg={6} sm={12}>
                                <p>Este tipo de Realidad Aumentada hace <strong>reconocimiento</strong> a un <strong>objeto real</strong> en específico, y sobre él se despliega la 
                                experiencia. Es el tipo de RA menos común, ya que requiere que el dispositivo tenga alta <strong>capacidad de procesamiento</strong> para detectar el
                                objeto.</p>
                            </Col>
                            <Col lg={6} sm={12}>
                                <YouTube className="VideoMarker" videoId="6W7_ZssUTDQ" opts={opts}/>
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div id="AplicacionesOb">
                    <Container fluid className="ContaiRAApps">
                        <Row className="RowTitRAApps">
                            <Typical
                                steps={['Aplicaciones de RA de objetos tangibles', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowApp">
                            <Col className="ColInfoRAApp" xl={7}>
                                <h2>Merge Cube</h2>
                                <p>Es un dispositivo denominado <strong>"juguete STEM"</strong> que hace uso de la RA en sus actividades. Merge permite a los usuarios 
                                <strong> sostener</strong> en sus manos objetos digitales, agregando valor y dinamismo al proceso de enseñanza-aprendizaje.</p>
                                <a className="aLink" href="https://mergeedu.com/" rel="noopener noreferrer" target="_blank" alt=""><span>Visitar</span></a>
                            </Col>
                            <Col id="Imagen1O" className="ColImgRAApp4" xl={5}>
                                <img src={Merge}></img>
                            </Col>
                        </Row>
                        <Row className="RowApp2">
                            <Col id="Imagen2O" className="ColImgRAApp5" xl={5}>
                                <img src={Warrior}></img>
                            </Col>
                            <Col id="InfoO" className="ColInfoRAApp" xl={7}>
                                <h2>Terracotta Warriors AR app</h2>
                                <p>Experiencia de RA diseñada para mejorar la exhibición Terracotta Warrior. La app permite <strong>escanear</strong> las diferentes estatuas y sobre ellas añadir
                                    información, armas y artefactos de estos guerreros, logrando que los visitantes <strong>comprendieran</strong> más la historia de los soldados de arcilla.</p>
                                    <a className="aLink" href="https://www.wikitude.com/showcase/terracotta-warriors-augmented-reality-at-the-franklin-institute/" rel="noopener noreferrer" target="_blank"><span>Visitar</span></a>
                            </Col>                            
                        </Row>
                    </Container>
                </div>

                <div className="BotVolType" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Types" text="Regresar"></Rboton>
                    </animated.div>
                </div>                
                                
                <Nav className="Bottombar">                                  
                    <div className="MenuBo">          
                        <Nav.Link href="#ConceptoOb"><div className="Campo">Concepto</div></Nav.Link>
                        <Nav.Link href="#AplicacionesOb"><div className="Campo">Aplicaciones</div></Nav.Link>
                    </div>
                </Nav>
                
            </div>
        );
    }

export default RAObjects;