import React, { Component, useState } from 'react';
import { Container, Col, Row } from 'react-bootstrap';
import './RVImportan.css';
import Rboton from '../../Botones/BackB';
import RV_Importan from '../../Images/RV/RV_Importan.svg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function RVImportan() {

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoImportRV">
                <Container fluid className="ContaiImportan">
                    <Col className="ColInfoImport" xl={7}>
                        <Row className="RowTitImportan">
                            <Typical
                                steps={['Importancia de la RV en las clases', 500]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowTextImport">
                            <p>La RV es una tecnología que nos permite <strong>transportarnos</strong> y <strong>explorar</strong> una gran variedad de entornos sin movernos de
                             donde estamos, haciendo que podamos vivir <strong>experiencias</strong> significativas y seguras. Por ejemplo en los laboratorios, donde podremos explorar instrumentos y combinaciones, o sumergirnos
                                en el oceano para conocer diferentes especies, o también, volver en el tiempo y vivir una época en específico para conocer de historia y culturas.</p>
                        </Row>
                        <Row className="RowEnlaces">
                            <h2>Enlaces a artículos</h2>
                            <a href="https://drive.google.com/file/d/1DxM15zRyq1FzgyBf8bdpPXEKrhD7AheE/view?usp=sharing" target="_blank" rel="noopener noreferrer">EduTrends - Realidad Virtual y Aumentada</a>
                            <a href="https://rockcontent.com/es/blog/realidad-virtual-en-la-educacion/" target="_blank" rel="noopener noreferrer">El futuro del aprendizaje: ¿Cómo aplicar la realidad virtual en la educación?</a>
                        </Row>
                    </Col>
                    <Col className="ColImgImporta" xl={5}>
                        <img src={RV_Importan}></img>                    
                    </Col>
                </Container>
                <div className="BtnBack" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Continuo" text="Regresar"></Rboton>
                    </animated.div>
                </div>
            </div>
        );
    }

export default RVImportan;