import React, { Component } from 'react';
import './VRHistory.css';
import { Container, Row, Col } from 'react-bootstrap';
import Side2 from '../RV/Sidebar2';
import Typical from 'react-typical';

import TimeLine from '../RV/Timeline/Timeline2';

class VRHistory extends Component {    
    render() {
        return(          
            <div className="ContHist">
                <Side2></Side2>
                <Container fluid className="contaiHistor">
                    <Row className="titlHis">
                        <Typical
                            steps={['Historia de la Realidad Virtual', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="InfoHisto">
                        <Col className="col2" lg={{ span: 9, offset: 2 }}>   
                            <TimeLine></TimeLine>
                        </Col>
                    </Row>
                </Container>        
            </div>
        );
    }
}

export default VRHistory;