import React, { Component, useState } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import YouTube from 'react-youtube';
import Rboton from '../../Botones/BackB';
import './EjemplosRA.css';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function ExamplesRA()  {

    const opts = {
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 0
            },
        };

        const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

        return(
            <div className="TodoEjemplos">
                <Container fluid className="ContaiEjemplosRA">
                    <Row className="RowTitEjemplosRA">
                        <Typical
                            steps={['Ejemplos de aplicación de la RA', 500]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowEjemplosRA">
                        <Col className="ColEjemploRA" xl={4}>
                            <div>
                                <h2>Ciencias</h2>
                                <p>Mr. Body de Merge Cube es una aplicación que le permite a los usuarios conocer sobre la <strong>anatomía</strong> del cuerpo humano en un holograma.</p>
                                <YouTube className="VideoEjemploRA" videoId="odWywru7e1U" opts={opts}/>
                            </div>
                        </Col>
                        <Col className="ColEjemploRA" xl={4}>
                            <div>
                                <h2>Química</h2>
                                <p>En este ejemplo los usuarios pueden hacer diferentes <strong>enlaces químicos</strong> , comprender su funcionamiento y ver el resultado en tiempo real.</p>
                                <YouTube className="VideoEjemploRA" videoId="kwTlxRX5Rxk" opts={opts}/>
                            </div>                        
                        </Col>
                        <Col className="ColEjemploRA" xl={4}>
                            <div>
                                <h2>Libros</h2>
                                <p>Una forma novedosa y divertida en que los usuarios pueden vivir las aventuras que encontramos en los libros.</p>
                                <YouTube className="VideoEjemploRA" videoId="F2ieduC3wCI" opts={opts}/>
                            </div>                        
                        </Col>
                    </Row>                                                            
                </Container>

                <div className="BtnBack" onClick={() => toggle(!state)}>
                    <animated.div
                        style={{
                        opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                        transform: x
                            .interpolate({
                            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                            output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                            })
                            .interpolate(x => `scale(${x})`)
                        }}>
                        <Rboton link="Continuo" text="Regresar"></Rboton>
                    </animated.div>
                </div>
            </div>
        );
    
}

export default ExamplesRA;