import React, {useEffect} from 'react';
import Aos from 'aos';
import "aos/dist/aos.css"

const TimelineItem2 =({ data2 }) => {
    useEffect(() => {
        Aos.init({ duration: 2000 });
    }, []);
    return(
    <div data-aos={"fade-up"} className="timeline-item2">
        <div className="timeline-item-content2">
            <span className='tag2' style={{ background: data2.category.color }}>
                    {data2.category.tag}
            </span>
            <time>{data2.date}</time>
            <div className="timeline-info2">
                <p>{data2.text}</p>
                <div className="timeline-img2"><img src={data2.imgIcon}></img></div>
            </div>            
            <span className="circle2"></span>
        </div>
    </div>
    );
}

export default TimelineItem2;