import React, { Component, useState } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import Audio1 from '../../Audios/Audio.wav';
import Rboton from '../../Home/Button';
import './Prototipado.css';
import exa from '../../Images/MapaNavegacion/HerramientasRARV.svg';
import brochure1 from '../../Images/brochure1.svg';
import Portada3 from '../../Images/Experiencias/Portada3.svg';
import Ficha3 from '../../Images/Experiencias/Ficha3.svg';
import iconFicha from '../../Images/Experiencias/Ficha_icon.svg';
import fases from '../../Images/Experiencias/Fases.svg';
import Plan3 from '../../Images/Experiencias/Plan3.svg';
import Brochure3 from '../../Images/Experiencias/Brochure3.jpg';
import Typical from 'react-typical';
import { useSpring, animated } from 'react-spring';

function Prototipado() {

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})
    
        return(
            <div className="AcercamientoRA">
                <div id="AboutAcercamiento">
                    <Container fluid className="ContaiAcerca">
                        <Row className="RowTitAcerca">
                            <Typical
                                steps={['Prototipando mi primer experiencia', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoAcerca">
                            <Col className="ColInfoAcercam" xl={7}>
                                <Col className="ColProposito" xl={6}>
                                    <div>
                                        <h2>Propósito</h2>
                                        <p>Busca abordar la <strong>solución creativa</strong> de una problemática dada. Poniendo en práctica los <strong>conocimientos</strong> adquiridos 
                                        en las 2 experiencias anteriores para la creación de un <strong>prototipo</strong> de baja fidelidad mediado por <strong>tecnología</strong>.</p>
                                        {/* <AudioPlayer
                                        autoPlay ={false}
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                        /> */}
                                    </div>                                
                                </Col>
                                <Col className="ColObjetivo" xl={6}>
                                    <div>
                                        <h2>Objetivo</h2>
                                        <p>Promover el <strong>desarrollo de habilidades</strong> para la solución de problemas, haciendo uso del método <strong>"Pensamiento de diseño" </strong>
                                        para la construcción de un prototipo.</p>
                                        {/* <AudioPlayer
                                        autoPlay ={false}
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                        />                                         */}
                                    </div>                                
                                </Col>
                            </Col>
                            <Col className="ImaAce" xl={5}>
                                <img src={Portada3}></img>
                            </Col>
                        </Row>  
                        <Row className="RowImgCont">
                            <div className="sgt"><a href="#ElementosAcerca"><h2>Continuar </h2></a></div>
                        </Row>                      
                    </Container>                    
                </div>

                <div id="ElementosAcerca">
                    <Container fluid className="ContaiElemAcerca">
                        <Row className="RowElemeAcer">
                            <Col className="ColInfoElemenAcer" xl={7}>
                                <Row className="RowTitElemAcer">
                                    <Typical
                                        steps={['Elementos integradores', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />
                                </Row>
                                <Row className="RowTextElemAcer">
                                    <p>Son tres documentos que deben tener todas las experiencias CONECTA-R con STEAM, y son los encargados de permitir que la experiencia 
                                    pueda ser <strong>repliacada</strong>.</p>                                    
                                </Row>
                                <Row className="AudioEle">
                                    <AudioPlayer
                                        autoPlay ={false}
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                    />
                                </Row>
                            </Col>
                            <Col className="ColSeleEleAcer" xl={5}>
                                <Col xl={6}>
                                    <Row className="ElemAcer">
                                        <a href="#FichaAcercamiento"><img src={exa}></img></a>
                                    </Row>
                                    <Row className="ElemAcer">
                                        <a href="#PlaneadorAcercamiento"><img src={exa}></img></a>
                                    </Row>                                
                                </Col>
                                <Col className="ColElemAcer" xl={6}>
                                    <a href="#BrochureAcercamiento"><img src={exa}></img></a>
                                </Col>                            
                            </Col>
                        </Row>
                        <Row className="RowImgCont">
                            <div className="sgt"><a href="#FichaAcercamiento"><h2>Ficha </h2></a></div>
                        </Row>
                    </Container>
                </div>

                <div id="FichaAcercamiento">
                    <Container fluid className="ContaiFichaAcer">
                        <Row>
                            <Col className="ColImaFichaAcerca" xl={5}>
                                <Row className="RowTitFichaAcerca">
                                    <Typical
                                        steps={['Ficha pedagógica', 1000]}
                                        loop={Infinity}
                                        wrapper="h1"
                                    />
                                </Row>
                                <Row className="RowImaFichaAcer">
                                        <img src={Ficha3}></img>
                                        <a className="aLink2" href="https://drive.google.com/file/d/16OvhxjMqihBKez_Vj6YFDaylMlqY1-y4/view?usp=sharing" target="_blank"><span>Ver ficha</span></a>                                    
                                </Row>
                            </Col>
                            <Col className="ColInfoFichaAcerca" xl={7}>
                                <Row className="RowTextoFichaAcer">
                                    <p>Es el elemento que describe el <strong>QUÉ</strong> de la experiencia. En este se define el público objetivo, el tiempo, los recursos y las 
                                competencias y habilidades STEAM que se harán presentes en la experiencia.</p>
                                    <div className="AudioFichaAcer">
                                    <AudioPlayer
                                        autoPlay ={false}
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                    />
                                    </div>
                                    
                                </Row>
                                <Row className="RowInfoExtraFichaAcer">
                                    <Col className="PublicoAcer">
                                        <h2>Público objetivo</h2>
                                        <h3>Estudiantes de 6-9 grado</h3>
                                    </Col>
                                    <Col>
                                        <img src={iconFicha}></img>
                                    </Col>
                                </Row>
                            
                            </Col>
                        </Row>
                        <Row className="RowImgCont">
                            <div className="sgt"><a href="#PlaneadorAcercamiento"><h2>Planeador </h2></a></div>
                        </Row>
                    </Container>
                </div>

                <div id="PlaneadorAcercamiento">
                    <Container fluid className="ContainPlaneadorAcer">
                        <Row className="RowPlaneAcer">
                            <Typical
                                steps={['Planeador (guía paso a paso)', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoPlaneAcer">
                            <Col className="ColInfoPlaneAcer" xl={6}>
                                <Row>
                                    <p>Es el <strong>CÓMO</strong> de nuestra experiencia, ya que describe una a una las actividades a desarrollar y la forma en la que se deben hacer.</p>
                                </Row>
                                <Row className="AudioPlaneAcer">
                                    <AudioPlayer
                                        autoPlay ={false}
                                        src={Audio1}                                    
                                        showJumpControls ={false}                                            
                                    />
                                </Row>
                            </Col>
                            <Col className="ColFasesPlaneAcer" xl={6}>
                                <Row className="RowTitFasesPlaneAcer">
                                    <h2>Fases de la experiencia</h2>
                                </Row>
                                <Row className="RowImgfasesPlaneAcer">
                                    <img src={fases}></img>
                                </Row>                            
                            </Col>
                        </Row>
                        <Row className="RowImgCont">
                            <div className="sgt1"><a href="#Actividades"><h2>Actividades </h2></a></div>
                        </Row>
                        <div id="Actividades">
                            <Row className="RowTitActvPlaneAcer">
                                <h1>Actividades clave</h1>
                            </Row>
                            <Row className="RowActividadesPlaneAcer">
                                <Col className="ColActiPlanAcer" xl={6}>
                                    <Row className="RowTitActivi1PlanAcer">
                                        <h3>Actividad 1.3: Construcción de un primer prototipo</h3>                                       
                                    </Row>
                                    <Row className="RowConteAct1PlanAcer">
                                        <Col>
                                            <p>La idea de esta actividad es llevar a los participantes a que <strong>conozcan</strong> y desarrollen cada una de las <strong>fases </strong>
                                             del método de "Pensamiento de diseño". En donde deben <strong>construir</strong> un puente de papel capaz de soportar gran variedad de pesos.</p>
                                        </Col>
                                        <Col>
                                            <img src={exa}></img>
                                        </Col>                                    
                                    </Row>                            
                                </Col>
                                <Col className="ColActiPlanAcer" xl={6}>
                                    <Row className="RowTitActivi1PlanAcer">
                                        <h3>Actividad 2.2: Análisis de un problemática dada</h3>                                  
                                    </Row>
                                    <Row className="RowConteAct1PlanAcer">
                                        <Col>
                                            <p>Los participantes se enfrentan a un caso de aplicación, en el que deben desempeñar un rol específico que los lleve en conjunto a
                                                una correcta solución, diseñarla y finalmente comenzar la construcción de su prototipo para la presentación final.</p>                                    
                                        </Col>
                                        <Col>
                                            <img src={exa}></img>                                    
                                        </Col>                                    
                                    </Row>                            
                                </Col>
                            </Row>
                            <Row className="RowContenListPlanAcer">
                                <Col className="ColListPlanAcer" xl={6}>
                                    <Row className="RowTitRecurPlaneAcer">
                                        <h2>Recursos</h2>
                                    </Row>
                                    <Row className="RowListRePlanAcer">
                                        <ul>
                                            <h3>Recursos para el facilitador</h3>
                                            <li>Documentos editables</li>
                                            <li>Infografía sobre el métoo "Pensamiento de diseño"</li>
                                            <h3><br></br>Recursos para el participante</h3>
                                            <li>Infografía de apoyo para la comprensión de las fases de diseño</li>
                                            <a href="https://drive.google.com/drive/folders/1-3A2Rb3IiMeghqxT6euKNi5-E9H0u1pP?usp=sharing" target="_blank"><h5><br></br><br></br>Haz clic aquí para obtener estos recursos</h5></a>
                                        </ul>
                                    </Row>                          
                                </Col>
                                <Col className="ColImgPlaneAcer" xl={6}>
                                    <Row className="RowImagPlanAc">
                                        <img src={Plan3}></img>
                                        <a className="aLink2" href="https://drive.google.com/file/d/1WkwXO-HJwkTCrSXlMW1xQBfvWFOK5qhX/view?usp=sharing" target="_blank"><span>Ver planeador</span></a>                            
                                    </Row>                                
                                </Col>
                            </Row>
                            <Row className="RowImgCont">
                                <div className="sgt"><a href="#BrochureAcercamiento"><h2>Brochure </h2></a></div>
                            </Row>
                        </div>
                    </Container>
                </div>

                <div id="BrochureAcercamiento">
                    <Container fluid className="ContaiBroAcer">
                        <Row className="RowTitBroAcer">
                            <Typical
                                steps={['Brochure', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />
                        </Row>
                        <Row className="RowInfoBroAcer">
                            <Col className="ColBrochure" xl={5}>
                                <img src={Brochure3}></img>
                                <a className="aLink2" href="https://drive.google.com/file/d/1nEx2YHUIN98L_K97aFg0dBQH-I3bIXla/view?usp=sharing" target="_blank"><span>Ver brochure</span></a>
                            </Col>
                            <Col className="ColInfoBroAcer" xl={7}>
                                <Row className="RowTextBroAcer">
                                    <p>Este documento es la presentación de la experiencia, va dirigido a las personas que estén interesadas en desplegar la experiencia, pues en él 
                                    encontrarán información de lo que trata y los materiales que se van a necesitar.</p>
                                </Row>                                                           
                            </Col>
                        </Row>
                    </Container>
                </div>

                <div onClick={() => toggle(!state)}>
                <animated.div className="BRegr"
                    style={{
                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                    transform: x
                        .interpolate({
                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                        })
                        .interpolate(x => `scale(${x})`)
                    }}>
                    <Rboton link="Experiencias" text="Regresar"></Rboton>
                    </animated.div>
                </div>
            </div>
        );
    }

export default Prototipado;