import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import portada from '../Images/Portada/Portada.svg';
import './Contact.css';
import axios from 'axios';
import Typical from 'react-typical';

class Contact extends Component {
    
    
    constructor(props) {
        super(props);
        this.state = {
          name: '',
          email: '',
          message: ''
        }
      }
    
      handleSubmit(e){
        e.preventDefault();
        axios({
          method: "POST", 
          url:"http://localhost:3002/send", 
          data:  this.state
        }).then((response)=>{
          if (response.data.status === 'success'){
            alert("Message Sent."); 
            this.resetForm()
          }else if(response.data.status === 'fail'){
            alert("Message failed to send.")
          }
        })
      }
    
      resetForm(){
        
         this.setState({name: '', email: '', message: ''})
      }
    
    
    render(){
    
        return(
            <div className="TodoContact">
                <Container fluid className="ContaiContact">
                    <Col xl={6}>
                        <Row className="RowTitContact">
                            <Typical
                                steps={['Contacto', 1000]}
                                loop={Infinity}
                                wrapper="h1"
                            />                        
                        </Row>
                        <Row className="RowTextQuieres">
                            <p>Para conocer más sobre el programa y las Experiencias STEAM, ingresa a: <a href="https://conectar.uao.edu.co" target="_blank" rel="noopener noreferrer">conectar.uao.edu.co</a></p>
                        </Row>

                        <Row className="Form">
                        <h2>Comienza tu formación con el curso "Desarrollo de Experiencias STEAM+MAKER"</h2>
                        <form id="contact-form" onSubmit={this.handleSubmit.bind(this)} method="POST">
                            <div className="form-group">
                                <label htmlFor="name">Nombre:</label>
                                <input type="text" className="form-control" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleInputEmail1">Correo electrónico:</label>
                                <input type="email" className="form-control" aria-describedby="emailHelp" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="message">Mensaje:</label>
                                <textarea className="form-control" rows="5"></textarea>
                            </div>
                            <button type="submit" className="btn btn-primary">Enviar</button>
                        </form>
                        </Row>




                    </Col>
                    <Col className="ColImg" xl={6}>
                        <img src={portada}></img>
                    </Col>
                </Container>
            </div>
        );
    }



    onNameChange(event) {
        this.setState({name: event.target.value})
    }

    onEmailChange(event) {
        this.setState({email: event.target.value})
    }

    onMessageChange(event) {
        this.setState({message: event.target.value})
    }




}

export default Contact;