import React from 'react';
import timelineData from '../Timeline/Data2';
import TimelineItem from './TimelineItem2';
// import './TimelineItem.css';

const Timeline2 = () => timelineData.length > 0 && (
    <div className="timeline-container2">
        {timelineData.map((data2, idx2) => (
            <TimelineItem data2={data2} key={idx2} />
        ))}
    </div>
);

export default Timeline2;