import React, { Component } from 'react';
import PopPop from 'react-poppop';
import Folleto from '../ConceptosRAyRV/PopUp';

class RAyRvBoton extends Component {

    constructor(props) {
        super(props);
        this.state = {
          show: false
        }
    }
    
    toggleShow = show => {
        this.setState({show});       
    }

 render() {
    const {show} = this.state;
    const {title, subtitle, text1, subtitle2, text2, subtitle3, text3, image, nameclass, dir, dir2, dir3} = this.props;
    const PopStyle = {
        overflow: 'hidden',
        maxWidth: '1050px'
    }

    return(
        <div>
            <img className={nameclass} src={image} onClick={() => this.toggleShow(true)}></img>
            <PopPop position="centerCenter"
                open={show}
                closeBtn={true}
                closeOnEsc={true}
                onClose={() => this.toggleShow(false)}
                closeOnOverlay={true}
                contentStyle={PopStyle}>

                <Folleto title={title} subtitle1={subtitle} text1={text1} subtitle2={subtitle2} text2={text2} subtitle3={subtitle3} text3={text3} dir={dir} dir2={dir2} dir3={dir3}></Folleto>
            </PopPop>
        </div>        
        );
    }
}

export default RAyRvBoton;