import React, { useState } from 'react';
import '../Home/Home.css';
import { Container, Row, Col } from 'react-bootstrap';
import Rboton from '../Botones/HomeB';
import { useSpring, animated } from 'react-spring';
import portada from '../Images/Portada/Portada.svg';
import Typical from 'react-typical';

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2]
const trans1 = (x, y) => `translate3d(${x / 15}px,${y / 15}px,0)`
const trans2 = (x, y) => `translate3d(${x / 8.2 + 27}px,${y / 8.2 - 160}px,0)`
const trans3 = (x, y) => `translate3d(${x / 6 + 90}px,${y / 6 + 50}px,0)`
const trans4 = (x, y) => `translate3d(${x / 6 + 230}px,${y / 4}px,0)`
const trans5 = (x, y) => `translate3d(${x / 3 - 100}px,${y / 4 + 160}px,0)`
const trans6 = (x, y) => `translate3d(${x / 6 - 270}px,${y / 7 + 170}px,0)`
const trans7 = (x, y) => `translate3d(${x / 4 + 170}px,${y / 4 - 150}px,0)`
const trans8 = (x, y) => `translate3d(${x / 7 -70}px,${y / 4 +30}px,0)`

function Home() {

    const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 10, tension: 550, friction: 140 } }))

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

    return(
        <div className="All">
            <Container fluid className="Container">           
                <Row className="Content">
                    <Col sm={12} lg={6} className="text">
                        <div className="Section1">


                        <Typical
                            steps={['Realidad Aumentada', 1000, 'Realidad Virtual', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                            <p>Tecnologías que están revolucionando la educación.</p>  
                            <div onClick={() => toggle(!state)}>
                                <animated.div
                                    style={{
                                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                                    transform: x
                                        .interpolate({
                                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                                        })
                                        .interpolate(x => `scale(${x})`)
                                    }}>
                                    <Rboton link="Intro" text="Descúbrelas"></Rboton>
                                </animated.div>
                            </div>
                        </div>
                    </Col>
                    <Col className="ColImgPortada" lg={6}>
                        <div class="container2" onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })}>
                            <animated.div class="card1" style={{ transform: props.xy.interpolate(trans1) }} />
                            <animated.div class="card7" style={{ transform: props.xy.interpolate(trans7) }} />
                            <animated.div class="card2" style={{ transform: props.xy.interpolate(trans2) }} />
                            <animated.div class="card3" style={{ transform: props.xy.interpolate(trans3) }} />
                            <animated.div class="card4" style={{ transform: props.xy.interpolate(trans4) }} />
                            <animated.div class="card5" style={{ transform: props.xy.interpolate(trans5) }} />
                            <animated.div class="card6" style={{ transform: props.xy.interpolate(trans6) }} />                        
                            <animated.div class="card8" style={{ transform: props.xy.interpolate(trans8) }} />
                        </div>
                        <img className="ImPor" src={portada}></img>
                    </Col>
                </Row>    
            </Container>        
        </div>
    );
}

export default Home;