import React from 'react';
import { Container, Row} from 'react-bootstrap';
import Side from './Sidebar2';
import './VRTypes.css';
import Card1 from '../../Images/RV/RVnoInmersiva.svg';
import Card2 from '../../Images/RV/RVSemiInmersiva.svg';
import Card3 from '../../Images/RV/RVInmersiva.svg';
import Rboton2 from '../../Botones/CardB';
import Typical from 'react-typical';

const VRType = () => (
    <div className="tipos">
        <Side></Side>
        <Container fluid className="ConteneTi">
            <Row className="TitTip">
                <Typical
                    steps={['Tipos de Realidad Virtual', 1000]}
                    loop={Infinity}
                    wrapper="h1"
                />
            </Row>
            <Row className="CardsRV">
                <div className="Cards">
                    <div className="Card1">
                        <img src={Card1}></img>
                        <div className="boto">
                            <Rboton2 link="RVnoInmersiva" text="Explorar"></Rboton2>
                        </div>
                    </div> 
                    <div className="Card1">
                        <img src={Card2}></img>
                        <div className="boto2">
                            <Rboton2 link="RVSemiInmersiva" text="Explorar"></Rboton2>
                        </div>
                    </div> 
                    <div className="Card1">
                        <img src={Card3}></img>
                        <div className="boto3">
                            <Rboton2 link="RVInmersiva" text="Explorar"></Rboton2>
                        </div>
                    </div>
                </div>                 
            </Row>
        </Container>        
    </div>
)

export default VRType;