import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import per from '../../Images/RV/Perife_Entrada.svg';
import pers from '../../Images/RV/Perife_Salida.svg';
import './Perifericos.css';
import Side2 from '../RV/Sidebar2';
import Typical from 'react-typical';
import Audios from '../../Audios/perifericos entrada.mp3';
import Audio2 from '../../Audios/perifericos salida.mp3';
import AudioPlayer from 'react-h5-audio-player';

class Perifericos extends Component {
    render() {
        return(
            <div className="TodoPerife">
                <Side2></Side2>
                <Container fluid className="ContaiPerife">
                    <Row className="RowTitPerife">
                        <Typical
                            steps={['Periféricos usados en la RV', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowTextPerife">
                        <p>Los sistemas de Realidad Virtual están compuestos por <strong>dispositivos</strong> que nos permiten <strong>interactuar</strong> con el entorno virtual.
                         Dichos dispositivos son los <strong>periféricos</strong> y existen tantos como los sentidos de una persona (vista, olfato, gusto, oido, tacto), y son los
                         <strong> encargados</strong> de que el usuario sienta la experiencia lo <strong>más real</strong> posible.</p>
                    </Row>
                    <Row id="RowContIma" className="RowImagPerife">
                        <Col id="ImgEn" className="ColImgPer">
                            <img src={per}></img>
                            <AudioPlayer className="AudioPerifeEntra"
                                autoPlay={0}
                                src={Audios}                                    
                                showJumpControls ={false}                                            
                            />
                        </Col>
                        <Col id="ImgSal" className="ColImgPer2">
                            <img src={pers}></img>
                            <AudioPlayer className="AudioPerifeSalida"
                                autoPlay={0}
                                src={Audio2}                                    
                                showJumpControls ={false}                                            
                            />
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Perifericos;