import React, { Component } from 'react';
import "../Map/Navigation.css";
import { Container, Row, Col } from 'react-bootstrap';
import Conceptos from "../Images/MapaNavegacion/ConceptosRARV.svg";
import Herramientas from "../Images/MapaNavegacion/HerramientasRARV.svg";
import DiseñoE from "../Images/MapaNavegacion/DiseñoExpe2.svg";
import IntroS from '../Images/MapaNavegacion/IntroSteam2.svg';
import {Link} from 'react-router-dom';
import Typical from 'react-typical';

class Navigation extends Component {

    render() {  

        return(

            <div className="todo">
                <Container fluid className="Con">
                    <Row>
                        <Typical
                            steps={['Mapa de navegación', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="Sec2">
                        <Col lg={6}>
                            <Row className="Row1">
                                <Col className="Col1">
                                    <Link to="Continuo"><img className="ImagentoZoom" src={Conceptos}></img></Link>
                                </Col>
                                <Col lg={2}></Col>                            
                            </Row>
                            <Row className="Row1">
                                <Col lg={7}></Col>
                                <Col className="Col1">
                                    <img className="ImagentoZoom" src={Herramientas}></img>
                                </Col>                                                        
                            </Row>
                        </Col>
                        <Col lg={6}>
                            <Row className="Row1">
                                <Col className="Col1">
                                    <Link to="STEAM"><img className="ImagentoZoom" src={IntroS}></img></Link>
                                </Col>
                                <Col lg={7}></Col>                            
                            </Row>
                            <Row className="Row1">
                                <Col lg={2}></Col>
                                <Col className="Col1">
                                    <Link to="Experiencias"><img className="ImagentoZoom" src={DiseñoE}></img></Link>
                                </Col>                                                        
                            </Row>
                        </Col>
                    </Row>           
                </Container>         
            </div>
        );
    }
}

export default Navigation;