import React, { useState } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Rboton from '../../Botones/BackB';
import Cardboard from '../../Images/RV/Cardboard.svg';
import './HMD.css';
import { useSpring, animated } from 'react-spring';
import Typical from 'react-typical';

function HMD() {

    const [state, toggle] = useState(true)
    const { x } = useSpring({ from: { x: 0 }, x: state ? 1 : 0, config: { duration: 1000}})

    return(
        <div className="Todohmd">
            <div id="SomeHMD">
                <Container fluid>
                    <Row className="RowTitCascos">
                        <Typical
                            steps={['Cascos de Realidad Virtual', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowContenInfoCascos">
                        <Col className="ColInfoCas" xl={4}>
                            <Row>
                                <h2>Samsung VR</h2>
                            </Row>
                            <Row className="RowInfoCa">
                                <p>Es un casco desarrollado por Samsung Electronics. Funciona introduciendo algunos modelos de celular de alta gama de Samsung, y se puede
                                    usar con control o sin él.</p>
                                <p>Si quieres saber más, haz clic <a href="https://www.samsung.com/es/wearables/gear-vr-sm-r325nzvaphe/" target="_blank" rel="noopener noreferrer">aquí</a></p>
                            </Row>                        
                        </Col>
                        <Col className="ColInfoCas" xl={4}>
                            <Row>
                                <h2>Merge Headset</h2>
                            </Row>
                            <Row className="RowInfoCa">
                                <p>Son las gafas desarrolladas por Merge. Sirven tanto para <strong>Realidad Aumentada</strong>, como para <strong>Realidad Virtual. </strong>
                                Introduciendo tu telefono móvil podrás ver las cosas desde otra perspectiva.</p>
                                <p>Si quieres saber más, haz clic <a href="https://mergeedu.com/headset" target="_blank" rel="noopener noreferrer">aquí</a></p>
                            </Row>                        
                        </Col>
                        <Col className="ColInfoCas" xl={4}>
                            <Row>
                                <h2>VR BOX</h2>
                            </Row>
                            <Row className="RowInfoCa">
                                <p>Es una de las mejores alternativas para el aula, gracias a que son <strong>fáciles de adquirir</strong> y la <strong>compatibilidad</strong> que
                                 tienen con casi todos los dispositivos. En ellas puedes ver videos 360°, juegos, RV y RA.</p>
                                <p>Si quieres saber más, haz clic <a href="http://www.vr-box.es/" target="_blank" rel="noopener noreferrer">aquí</a></p>
                            </Row>                        
                        </Col>
                    </Row>
                    <Row  id="InvitaSeguir" className="RowImgCont">
                        <div className="sgt"><a href="#CreaTusGafas"><h2>Crear </h2></a></div>
                    </Row>
                </Container>
            </div>   

            <div id="CreaTusGafas">
                <Container fluid className="ContaCreaTu">
                    <Row className="RowTitCreaTu">
                        <Typical
                            steps={['Crea tu propio casco de RV', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>
                    <Row className="RowContCardTu">                        
                        <Col className="ColImgCardB">
                            <img src={Cardboard}></img>
                        </Col>
                        <Col className="ColInfoCardB">
                            <h2>Google Cardoard</h2>
                            <p>Es un <strong>visor</strong> de Realidad Virtual, el cual puedes comprar o <strong>fabricar</strong> tú mismo. Estas gafas permiten experimentar la RV
                            de manera sencilla, divertida y accesible.<br></br><br></br>En el siguiente botón encontrarás los kits necesarios para que construyas tus propias
                            gafas de Google Cardboard.</p>     
                            <a className="aLink2" href="https://arvr.google.com/intl/es-419_mx/cardboard/manufacturers/" target="_blank" rel="noopener noreferrer"><span>Descubre</span></a>                   
                        </Col>
                    </Row>
                </Container>                
            </div> 

            <div className="BotVolExp" onClick={() => toggle(!state)}>
                <animated.div
                    style={{
                    opacity: x.interpolate({ range: [0, 1], output: [0.3, 1] }),
                    transform: x
                        .interpolate({
                        range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
                        output: [1, 0.97, 0.9, 1.1, 0.9, 1.1, 1.03, 1]
                        })
                        .interpolate(x => `scale(${x})`)
                    }}>
                    <Rboton link="Technology" text="Regresar"></Rboton>
                </animated.div>
            </div>        
        </div>
    );
}

export default HMD;