import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Cel from '../../Images/Cel.svg';
import Side from '../Sidebar';
import './ARTools.css';

class ARTools extends Component {
    render() {
        return(
            <div className="TodoTools">
                <Side></Side>
                <Container fluid className="ContaiTools1">
                    <Row className="RowTitToolsRA">
                        <h1>Herramientas para la creación de RA</h1>
                    </Row>
                    <Row className="RowInfoTools">
                        <Col className="ColInfoTools">                            
                            <p>Existen diferentes aplicaciones y programas para el <strong>desarrollo</strong> de experiencias de Realidad Aumentada. Estas herramienta van desde lo más
                             simple, en donde no se necesita saber de modelado o programación, hasta las más complejas, como los motores de videojuegos.</p>
                            <a href="HerramientasRA" className="aLink"><span>Descubre más</span></a>                        
                        </Col>
                        <Col className="ColImgTools"> 
                            <img src={Cel}></img>
                        </Col>
                    </Row>                    
                </Container>
            </div>
        );
    }
}

export default ARTools;