import React, { Component } from 'react';
import './Funtion.css';
import { Container, Row, Col } from 'react-bootstrap';
import IFunc from '../Images/InfograFunc.png';
import imgI from '../Images/Bot_Info.svg';
import Side from './Sidebar';
import {OverlayTrigger, Popover} from 'react-bootstrap';
import Typical from 'react-typical';

class Funci extends Component {
    render() {
        return(
            <div className="ContFunc">        
                <Side></Side>
                <Container fluid className="contaiFuncio">
                    <Row className="titlFunc">
                        <Typical
                            steps={['Funcionamiento de la Realidad Aumentada', 500]}
                            loop={Infinity}
                            wrapper="h1"
                        />
                    </Row>     
                    <Row className="InfogFun">
                        <Col className="colSec2" lg={{ span: 9, offset: 3 }}>
                            <div className="conteneImg">
                                <img className="Imgifogr" src={IFunc}></img>                        

                                <OverlayTrigger
                                    trigger="click"
                                    key="bottom"
                                    placement="bottom"
                                    rootClose="true"
                                    overlay={
                                        <Popover>
                                            <Popover.Title><strong>Activadores</strong></Popover.Title>
                                            <Popover.Content>
                                                Los activadores pueden ser <strong>códigos QR, objetos 3D o imágenes</strong> y sobre ellos se desplegará la información de RA cuando sean detectados por el software.
                                            </Popover.Content>
                                        </Popover> 
                                    }>
                                    <img className="BI" src={imgI}></img>
                                </OverlayTrigger>  

                                <OverlayTrigger
                                    trigger="click"
                                    key="top"
                                    placement="top"
                                    rootClose="true"
                                    overlay={
                                        <Popover>
                                            <Popover.Title><strong>Aplicaciones</strong></Popover.Title>
                                            <Popover.Content>
                                                Software encargado de detectar y procesar el activador para <strong>agregar</strong> los elementos virtuales como <strong>modelos 3D, textos, imágenes y videos</strong>.
                                            </Popover.Content>
                                        </Popover>
                                    }>
                                    <img className="BI2" src={imgI}></img>
                                </OverlayTrigger>                

                                <OverlayTrigger
                                    trigger="click"
                                    key="right"
                                    placement="right"
                                    rootClose="true"
                                    overlay={
                                        <Popover>
                                            <Popover.Title><strong>Dispositivos</strong></Popover.Title>
                                            <Popover.Content>
                                                El dispositivo debe tener una pantalla, la cual se encarga de <strong>mostrar</strong> la <strong>combinación</strong> de lo <strong>real</strong> y lo <strong>virtual</strong> al mismo tiempo.
                                            </Popover.Content>
                                        </Popover>
                                    }
                                    >
                                    <img className="BI3" src={imgI}></img>
                                </OverlayTrigger> 

                                <OverlayTrigger
                                    trigger="click"
                                    key="left"
                                    placement="left"
                                    rootClose="true"
                                    overlay={
                                        <Popover>
                                            <Popover.Title><strong>Cámara</strong></Popover.Title>
                                            <Popover.Content>
                                                Encargada de <strong>tomar</strong> la información del <strong>mundo real</strong> y <strong>pasarla</strong> al software de RA.
                                            </Popover.Content>
                                        </Popover>
                                    }>
                                    <img className="BI4" src={imgI}></img>
                                </OverlayTrigger>                                         
                            </div>
                        </Col>       
                    </Row>
                </Container>               
            </div>
        );
    }
}

export default Funci;