import React from 'react';
import '../About/About.css';
import { Container, Row, Col } from 'react-bootstrap';
import LogoUAO from '../Images/LogoUAO.png';
import Perfil from '../Images/Perfil.svg';
import Typical from 'react-typical';

const About = () => (
    <div className="AbAll">
        <Container fluid className="AboContent">
            <Row className="TextSec">
                <Col>
                    <Typical
                        steps={['¿De qué trata esta plataforma?', 1000]}
                        loop={Infinity}
                        wrapper="h1"
                    />
                    <p>Es una plataforma web con elementos interactivos en la cual encontrarás todos los recursos y ejemplos necesarios para la creación de experiencias de
                        <strong> Realidad Aumentada</strong> y <strong>Realidad Virtual</strong> bajo el enfoque STEAM (Science, Technology, Engineering, Art and Mathematics).</p>
                    <div className="Desarrollo">
                        <h2>Desarrollada por</h2>
                        <div className="InfoDesa">
                            <div className="Perfilimg">
                                <img src={Perfil}></img>
                            </div>
                            <div className="InfoName">
                                <h3>Carlos Alberto Díaz Fuertes</h3>
                                <h3>Ing. Multimedia</h3>
                            </div>
                        </div>                     
                    </div>
                </Col>                
            </Row>
            <Row className="Logo">
                <img src={LogoUAO}></img>
            </Row>
        </Container>
        
    </div>
)

export default About;