import React, { Component } from 'react';
import {Navbar, Nav } from 'react-bootstrap';
import Logo from '../Images/Gafas.svg';
import './MainMenu.css';
import {Link} from 'react-router-dom';

class MainMenu extends Component {
    render() {
        return (
            <Navbar collapseOnSelect expand="lg" sticky="top" variant="dark" className="Menu">
                <Link to ="/">
                    <Navbar.Brand href="#Home">
                        <img src={Logo} width="50" height="50"/>
                        <Navbar.Brand className="NavText">
                            CONECTA-R con STEAM
                        </Navbar.Brand>
                    </Navbar.Brand>
                </Link>
                

                <Navbar.Toggle aria-controls="basic-navbar-nav" className="o-imgbrand"/>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">

                    </Nav>
                    <Nav>
                        <Nav.Link href="Navigation">Mapa de navegación</Nav.Link>
                        <Nav.Link href="About">Sobre la plataforma</Nav.Link>
                        <Nav.Link href="Contact">Contacto</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default MainMenu;