import React, { Component } from 'react';
import '../ConceptosRAyRV/Continuo.css';
import { Container, Row, Col } from 'react-bootstrap';
import Infogra from '../Images/RARVCont.svg';
import Ver1 from '../Images/Ver1.svg';
import Ver2 from '../Images/Ver2.svg';
import ARButton from '../ConceptosRAyRV/BotonRARV';
import Typical from 'react-typical';

class Continuo extends Component { 
    render () {       
        return (
            <div className="World">
                <Container fluid className="ContCont">
                    <Row className="TitCont">
                        <Typical
                            steps={['Continuo de la virtualidad', 1000]}
                            loop={Infinity}
                            wrapper="h1"
                        />                        
                    </Row>
                    <Row className="Conteni">
                        <Col className="Desc" lg={4}>
                            <p>Es un concepto que describe la <strong>conexión</strong> que hay entre los <strong>entornos reales</strong> y los <strong>entornos virtuales</strong>. 
                               Por lo que en el extremo izquierdo se encuentra el  mundo real tal cual como lo conocemos, y al extremo derecho se encuentra un mundo completamente
                               virtual.<br></br><br></br>En medio de ellos, tenemos diferentes combinaciones de <strong>objetos virtuales</strong> con la realidad, y se denomina Realidad Mixta,
                               en donde de izquierda a derecha aumenta la cantidad de elementos generados por computador.</p>
                        </Col>
                        <Col lg={7}>
                            <div>
                                <img className="Infografia" src={Infogra}></img>
                                
                                <ARButton title={"REALIDAD AUMENTADA"} 
                                subtitle={"Definición"}
                                text1={"Es el conjunto de tecnologías que permiten que un usuario visualice parte del mundo real a través de un dispositivo tecnológico con información gráfica añadida por este."} 
                                subtitle2={"Casos de aplicación"}
                                text2={"La RA puede ser aplicada en cualquier asignatura como herramienta de apoyo que aumenta la motivación de los estudiantes por aprender nuevos temas, ya que pueden ver un objeto cobrar vida e interactuar con él de una nueva forma."}
                                subtitle3={"Importancia de la RA en las clases"}
                                text3={"Su importancia se debe gracias a la posibilidad de añadir información virtual al mundo físico, lo que facilita la comprensión rápida de los temas."}
                                image={Ver1} 
                                dir={"History"}
                                dir2={"ExamplesRA"}
                                dir3={"RAImportan"}
                                nameclass={"Ver1"}/>

                                <ARButton title={"REALIDAD VIRTUAL"} 
                                subtitle={"Definición"}
                                text1={"Es un entorno de escenas u objetos virtuales de apariencia real. Están generados totalmente por computadora y crean en el usuario la sensación de que verdaderamete está dentro de él y que es real."} 
                                subtitle2={"Casos de aplicación"}
                                text2={"La RV nos permite vivir experiencias únicas de forma segura. Gracias a sus grados de inmersión, nos lleva a estar en situaciones que de otra forma serían inimaginables."}
                                subtitle3={"Importancia de la RV en las clases"}
                                text3={"Esta tecnología agrega dinamismo a las actividades que realizan los estudiantes dentro y fuera del aula, fomentando en ellos el interés por conocer nuevas cosas."}
                                image={Ver2} 
                                dir={"HistoryVR"}
                                dir2={"ExamplesRV"}
                                dir3={"RVImportan"}
                                nameclass={"Ver2"}/>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Continuo;