import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import '../ConceptosRAyRV/popUp.css';
import Rboton from '../Botones/PopUpB';

const PopUp = (props) => (
    <div className="FondoFo">
        <Container fluid className="contentFo">
            <Row className="TitleFolleto">
                    <h1>{props.title}</h1>
            </Row>
            <Row className="DescriptionFolleto">
                <Col>
                    <h2>{props.subtitle1}</h2>
                    <p className="Tx1">{props.text1}</p>                                  
                    <div className="b">
                        <Rboton link={props.dir} text="Ver más"></Rboton>
                    </div>             
                </Col>       

                <Col className="Col2">
                    <h2>{props.subtitle2}</h2>
                    <p className="Tx1">{props.text2}</p>
                    <div className="b">
                        <Rboton link={props.dir2} text="Ejemplos"></Rboton>
                    </div>                                    
                </Col>

                <Col>
                    <h2>{props.subtitle3}</h2>
                    <p className="Tx1">{props.text3}</p>
                    <div className="b">
                        <Rboton link={props.dir3} text="Explorar"></Rboton>
                    </div>                    
                </Col>     
            </Row>
        </Container>        
    </div>
)

export default PopUp;